.player__play-button {
  grid-area: play;
  width: 20px;
  height: 16px;
  transition: opacity .2s ease-in;
}

.player__play-button:hover {
  cursor: pointer;
  opacity: .7;
}

@media screen and (max-width: 1024px) {
  .player__play-button {
    width: 18px;
    height: 15px;
  }
}

@media screen and (max-width: 426px) {
  .player__play-button {
    width: 16px;
    height: 13px;
  }
}
