.player__more-button {
  grid-area: more;
  transition: opacity .2s ease-in;
  width: 24px;
  height: 24px;
}

.player__more-button:hover {
  cursor: pointer;
  opacity: .7;
}

@media screen and (max-width: 426px) {
  .player__more-button {
    width: 16px;
    height: 16px;
  }
}
