.player__song-title_active {
    /* padding-left: 50px; */
    animation: scroll 7s infinite linear;
}

@keyframes scroll {
    0% {
        transform: translate(100%, 0)
    }
    50% {
        transform: translate(0, 0)
    }
    100% {
        transform: translate(-100%, 0)
    }
}

